* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --black: #2a2a2a;
  --white: #fff;
  --gray: #5e6573;
}

html {
  min-width: 0;
  color: var(--black);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

body {
  padding: 20px;
  font-size: 16px;
  line-height: 1.35;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  margin-bottom: 12px;
}

li {
  margin-top: 8px;
  margin-left: 14px;
}

section {
  page-break-inside: avoid;
}

hr {
  border-color: var(--white);
  margin-top: 20px;
  margin-bottom: 20px;
}

.container {
  margin: 0 auto;
}

.titles {
  margin-bottom: 16px;
  font-size: 24px;
}

.bio {
  margin-bottom: 40px;
}

.bio__name {
  font-size: 30px;
}

.bio__contact {
  flex-direction: column;
  gap: 8px;
  display: inline-flex;
}

.bio__contact a {
  color: var(--black);
  font-size: 20px;
}

.about__desc > * + * {
  margin-top: 12px;
}

.works__comp__name {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.works__comp__name h4 {
  margin-bottom: 0;
  font-size: 18px;
}

.works__role {
  color: var(--gray);
  margin-bottom: 8px;
}

.works__box:not(:last-child) {
  margin-bottom: 24px;
}

.edu__title {
  justify-content: space-between;
  display: flex;
}

.edu__title h4 {
  max-width: 200px;
  margin-bottom: 8px;
}

@media screen and (width >= 760px) {
  .container {
    max-width: 680px;
  }

  .edu__title h4 {
    max-width: 100%;
  }
}

@page {
  size: A4 portrait;
  margin: 22px;
}
